h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  border: none;
  margin: 0;
}

b {
  display: block;
}

button {
  cursor: pointer;
  padding: 0;
}

a {
  color: inherit;
  cursor: pointer;
  font-style: normal;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #5c6d7d;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 458px;
